import Image from "next/image"

export default function Footer() {
  return (
    <footer className="bg-[#e6e6e6] text-black py-12 md:py-16">
      <div className="max-w-6xl mx-auto px-6">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-10 md:gap-8">
          {/* ================= LEFT: Logo + Info + Social ================= */}
          <div>
            <div className="flex items-center mb-4">
              <Image
                src="/images/worklylogo.png"
                alt="Workly Logo"
                width={130}
                height={27}
                className="mr-2"
              />
            </div>

            <p className="text-sm text-[#444444] max-w-xs leading-relaxed mb-6 font-inter">
              The purpose of a FAQ is generally to provide information on frequent
              questions or concerns.
            </p>

            {/* ✅ Social Icons */}
            <div className="flex gap-3">
              {[
                { name: "twitter", link: "/images/icons/twitter.svg" },
                { name: "facebook", link: "/images/icons/facebook.svg" },
                { name: "instagram", link: "/images/icons/instagram.svg" },
                { name: "github", link: "/images/icons/github.svg" },
              ].map((icon) => (
                <a
                  key={icon.name}
                  href="#"
                  className="w-10 h-10 flex items-center justify-center rounded-full bg-white border border-[#f5eede] shadow-sm transition-all duration-300 hover:bg-[#0072F5] hover:border-[#0072F5] hover:-translate-y-1"
                >
                  <Image
                    src={icon.link}
                    alt={icon.name}
                    width={18}
                    height={18}
                    className="transition-all duration-300 invert-0 brightness-0 hover:invert"
                  />
                </a>
              ))}
            </div>
          </div>

          {/* ================= CENTER: Company + Help ================= */}
          <div className="grid grid-cols-2 gap-6">
            {/* Company */}
            <div>
              <h4 className="font-semibold mb-3 text-base font-inter">Company</h4>
              <ul className="space-y-2 text-sm text-[#333333]">
                {["All Service", "FAQs", "About us"].map((item) => (
                  <li key={item}>
                    <a
                      href="#"
                      className="hover:text-[#0072F5] transition-colors duration-200"
                    >
                      {item}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            {/* Help */}
            <div>
              <h4 className="font-semibold mb-3 text-base font-inter">Help</h4>
              <ul className="space-y-2 text-sm text-[#333333]">
                {[
                  "Customer Support",
                  "Terms & Conditions",
                  "Privacy Policy",
                ].map((item) => (
                  <li key={item}>
                    <a
                      href="#"
                      className="hover:text-[#0072F5] transition-colors duration-200"
                    >
                      {item}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>

          {/* ================= RIGHT: Newsletter ================= */}
          <div>
            <h4 className="font-semibold mb-3 text-base font-inter">
              Subscribe to Newsletter
            </h4>

            <div className="flex bg-white rounded-xl overflow-hidden border border-gray-200 max-w-xs h-11">
              <input
                type="email"
                placeholder="Enter email address"
                className="flex-1 px-4 text-sm outline-none text-gray-700 placeholder-gray-400"
              />
              <button
                type="submit"
                className="bg-[#0072F5] text-white text-sm font-semibold px-4 transition-all duration-200 hover:bg-[#005fd1]"
              >
                Join now
              </button>
            </div>
          </div>
        </div>

        {/* Divider */}
        <div className="border-t border-gray-300 my-8"></div>

        {/* Copyright */}
        <p className="text-center text-sm text-[#444444]">
          © Copyright {new Date().getFullYear()}, All Rights Reserved by{" "}
          <span className="font-semibold">Workly</span>
        </p>
      </div>
    </footer>
  )
}
